import { useState, useEffect, useCallback } from 'react';
import config from './config';

const API_ENDPOINT = config.apiEnpoint;

export const useSuggest = (query, initData) => {
   const url = `${API_ENDPOINT}/suggest?q=${query}`;
   const [
     doSuggest,
     {
       data: suggestData,
       loading: suggestLoading, error: suggestError
     }
    ] = useRequest(url, {method: 'GET', initValue: initData});

   useEffect(() => {
     if (!query) {
       return;
     }
     console.log(`Searching: ${query} ...`);
     doSuggest();
   }, [doSuggest, query]);

   return {suggestData, suggestLoading, suggestError};
};

export function useRequest(
  url,
  opts = {
    method: 'GET',
    body: undefined,
    initValue: [],
  },
) {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(opts.initValue);
  const [called, setCalled] = useState(false);
  const [startRequest, setStartRequest] = useState(false);
  const [error, setError] = useState();

  const getData = useCallback(() => {
    setCalled(true);
    setStartRequest(true);
  }, []);

  useEffect(() => {
    if (!startRequest) {
      return;
    }
    if (!url) {
      return
    }

    console.log(`Requesting url=${url}`);

    setStartRequest(false);
    setLoading(true);

    const options = {
      method: opts.method,
    };
    if (opts.method !== "GET") {
      options.headers = {'Content-Type': 'application/json'};
    }
    if (opts.data) {
      options.body = JSON.stringify(opts.body);
    }
    const retrieveData = async () => {
      try {
        const response = await fetch(url, options);
        const responseData = await response.json();
        setData(responseData);
        setLoading(false);
      } catch (err) {
        setError(err);
        setLoading(false);
      }
    };
    retrieveData();
  }, [opts, startRequest, url]);

  return [getData, { called, loading, error, data }];
}


