import React, { useState, useEffect } from "react";
// import "./App.css";
import { ThemeProvider, theme } from "./style";
import GlobalStyles from "./style/GlobalStyles";
import { getInputs } from "./utils";
import InstantSearchWraper from "./InstantSearchWraper";
import { Box } from "./components";
import  MobileInstantSearchWraper from "./MobileInstantSearchWraper";

function App() {
  const [instantSearchs, setInstantSearchs] = useState([]);
  console.log("Render App ...")

  useEffect(() => {
    const inputs = getInputs();
    setInstantSearchs(inputs.map((input, index) => <InstantSearchWraper key={index} input={input} />));
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyles />
      <Box id="flashsearch-instant-search">
      {instantSearchs}
      </Box>
      <Box id="flashsearch-instant-search-mb">
        <MobileInstantSearchWraper />
      </Box>
    </ThemeProvider>
  );
}
export default App;
