import React, {useEffect} from "react";
import Label from "./Label";
import Products from "./Products";
import Suggestions from "./Suggestions";
import Link from "../Link";
import { Box, BoxShadow } from "../Grid";
import Text from "../Text";
import ViewAllResult from "./ViewAllResults";
import styled from "../../style";
import Collections from "./Collections";
import Pages from "./Pages";

const StyledWraper = styled(BoxShadow)`
  overscroll-behavior-y: contain;
`;

const InstantSearch = props => {
  const {
    suggestions, collections, pages, products, didYouMean, productsPerPage,
    query, enable,
    // position
    top, left, maxHeight,
  } = props;

  useEffect(() => {
    console.log(`Suggestions: ${suggestions}`);
    console.log(`collections: ${collections}`);
    console.log(`pages: ${pages}`);
  }, []);

  // did you mean markup
  const didYouMeanMarkup = didYouMean ? (
    <Text textAlign="center" py={2}>
      Sorry, nothing found for <Text as="span" fontWeight="bold">{query}</Text>.&nbsp;
      Did you mean: <Link href="#mean" bg="grey.0" px={2} py={1} borderRadius={4}><Text as="span">{didYouMean}</Text></Link> ?
    </Text>
  ): null;

  // View all results markup
  const viewAllResultMarkup = products.length === productsPerPage ? (
    <ViewAllResult query={query} />
  ) : null;

  return (
    <StyledWraper
      bg="background"
      position="fixed"
      overflow="auto"
      boxShadow="instantSearch"

      border={["none", "none", 1, 1]}
      borderRadius={[0, 0, 1, 1]}
      zIndex={2}
      display={enable ? "block" : "none"}
      top={['6px', '6px', `${top}px`, `${top}px`]}
      left={[0, 0, `${left}px`, `${left}px`]}
      pt={["mobileInstantSearchPt", "mobileInstantSearchPt", 0, 0]}
      width={["100%", "100%", 2, 2]}
      maxHeight={["calc(100% - 6px)", "calc(100% - 6px)", `${maxHeight}px`, `${maxHeight}px`]}
      onTouchMove={() => document.activeElement.blur()}
    >
      <Box mx={[3, 3, 4, 4]} my={[2, 2, 3, 3]}>
        <Label>POPULAR SUGGESTIONS</Label>
        <Suggestions suggestions={suggestions} query={query}/>
        <Collections collections={collections} query={query}/>
        <Pages pages={pages} query={query}/>
        {didYouMeanMarkup}
        <Label>PRODUCTS</Label>
        <Products products={products}/>
      {viewAllResultMarkup}
      </Box>
    </StyledWraper>
  );
};

export default InstantSearch;
